:root {
  --primary-dark: #1a1a1a;
  --secondary-dark: #2a2a2a;
  --text-light: #e0e0e0;
  --text-muted: #b0b0b0;
  --accent-red: #e50914;
  --accent-blue: #007bff;
  --border-dark: #3a3a3a;
  --shimmer-bg-dark: #333;
  --shimmer-line-dark: #444;
  --primary-light: #f0f2f5;
  --secondary-light: #fff;
  --text-dark: #333;
  --text-dark-muted: #666;
  --border-light: #e0e0e0;
  --shimmer-bg-light: #e0e0e0;
  --shimmer-line-light: #f5f5f5;
}

body {
  background-color: var(--primary-dark);
  color: var(--text-light);
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  transition: background-color .3s, color .3s;
}

body.light-mode {
  background-color: var(--primary-light);
  color: var(--text-dark);
}

.app {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  background-color: var(--secondary-dark);
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 15px 40px;
  transition: background-color .3s, box-shadow .3s;
  display: flex;
  box-shadow: 0 6px 20px #0006;
}

body.light-mode .header {
  background-color: var(--secondary-light);
  box-shadow: 0 6px 20px #0000001a;
}

.logo-container .logo {
  filter: drop-shadow(0 0 8px #fff6) brightness(1.2);
  cursor: pointer;
  width: 100px;
  transition: transform .3s, filter .3s;
}

.logo-container .logo:hover {
  filter: drop-shadow(0 0 12px #fff9) brightness(1.4);
  transform: scale(1.08)rotate(-1deg);
}

.nav-items {
  align-items: center;
  display: flex;
}

.nav-items ul {
  gap: 35px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-items ul li {
  cursor: pointer;
  color: var(--text-light);
  font-size: 1.1rem;
  font-weight: 500;
  transition: color .2s;
}

body.light-mode .nav-items ul li {
  color: var(--text-dark);
}

.nav-items ul li:hover {
  color: var(--accent-red);
}

.nav-items button {
  background-color: var(--accent-red);
  color: #fff;
  cursor: pointer;
  letter-spacing: .5px;
  border: none;
  border-radius: 5px;
  margin-left: 30px;
  padding: 10px 25px;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .3s, transform .2s;
}

.nav-items button:hover {
  background-color: #cc0811;
  transform: translateY(-2px);
}

body.light-mode .nav-items button {
  background-color: var(--accent-blue);
}

body.light-mode .nav-items button:hover {
  background-color: #0056b3;
}

.body {
  padding: 20px 0;
}

.news-container {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
  display: grid;
}

.news-card {
  background-color: var(--secondary-dark);
  border-radius: 8px;
  flex-direction: column;
  min-height: 450px;
  transition: transform .3s, box-shadow .3s, background-color .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 25px #00000080;
}

body.light-mode .news-card {
  background-color: var(--secondary-light);
  box-shadow: 0 8px 25px #00000026;
}

.news-card:hover {
  transform: scale(1.03);
  box-shadow: 0 12px 35px #000000b3;
}

body.light-mode .news-card:hover {
  box-shadow: 0 12px 35px #00000040;
}

.news-card-content {
  flex-direction: column;
  flex-grow: 1;
  padding: 20px;
  display: flex;
}

.news-card h2 {
  color: var(--text-light);
  min-height: 3.9rem;
  margin: 0 0 10px;
  font-size: 1.3rem;
  line-height: 1.3;
  overflow: hidden;
}

body.light-mode .news-card h2 {
  color: var(--text-dark);
}

.news-card p {
  color: var(--text-muted);
  flex-grow: 1;
  margin-bottom: 15px;
  font-size: .9rem;
  line-height: 1.5;
  overflow: hidden;
}

body.light-mode .news-card p {
  color: var(--text-dark-muted);
}

.news-card a {
  color: var(--accent-red);
  align-self: flex-start;
  font-weight: 600;
  text-decoration: none;
  transition: color .2s;
  display: inline-block;
}

.news-card a:hover {
  color: #ff4d4d;
}

.news-card .news-date {
  color: var(--text-muted);
  border-top: 1px solid var(--border-dark);
  margin-top: auto;
  padding-top: 10px;
  font-size: .8rem;
}

.single-news-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.single-news-container .news-card {
  flex-direction: column;
  width: 100%;
  max-width: 900px;
  min-height: 500px;
  margin-bottom: 20px;
  padding: 0;
  display: flex;
}

.news-card img {
  object-fit: cover;
  border-radius: 8px 8px 0 0;
  width: 100%;
  height: 250px;
}

.navigation-buttons {
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.next-news-button, .prev-news-button {
  background-color: var(--accent-red);
  color: #fff;
  cursor: pointer;
  letter-spacing: .8px;
  border: none;
  border-radius: 8px;
  padding: 12px 30px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: background-color .3s, transform .2s, box-shadow .3s;
  box-shadow: 0 4px 10px #0000004d;
}

.next-news-button:hover, .prev-news-button:hover {
  background-color: #cc0811;
  transform: translateY(-2px);
  box-shadow: 0 6px 15px #0006;
}

body.light-mode .next-news-button, body.light-mode .prev-news-button {
  background-color: var(--accent-blue);
  box-shadow: 0 4px 10px #0000001a;
}

body.light-mode .next-news-button:hover, body.light-mode .prev-news-button:hover {
  background-color: #0056b3;
  box-shadow: 0 6px 15px #0003;
}

.shimmer-single-container {
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  display: flex;
}

.shimmer-single-card {
  background-color: var(--secondary-dark);
  border-radius: 8px;
  flex-direction: column;
  width: 90%;
  max-width: 900px;
  min-height: 600px;
  animation: 1.5s ease-in-out infinite pulse;
  display: flex;
  overflow: hidden;
  box-shadow: 0 8px 25px #00000080;
}

body.light-mode .shimmer-single-card {
  background-color: var(--secondary-light);
  box-shadow: 0 8px 25px #00000026;
}

.shimmer-img {
  background-color: var(--shimmer-bg-dark);
  border-radius: 8px 8px 0 0;
  width: 100%;
  height: 250px;
}

body.light-mode .shimmer-img {
  background-color: var(--shimmer-bg-light);
}

.shimmer-details {
  flex-direction: column;
  gap: 16px;
  padding: 24px 32px;
  display: flex;
}

.shimmer-title {
  background-color: var(--shimmer-line-dark);
  border-radius: 4px;
  width: 70%;
  height: 24px;
}

.shimmer-desc {
  background-color: var(--shimmer-line-dark);
  border-radius: 4px;
  width: 100%;
  height: 16px;
}

.shimmer-desc.short {
  width: 60%;
}

.shimmer-button {
  background-color: var(--shimmer-line-dark);
  border-radius: 6px;
  width: 120px;
  height: 36px;
  margin-top: 20px;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .7;
  }

  100% {
    opacity: 1;
  }
}

@media (width <= 1024px) {
  .app {
    padding: 15px;
  }

  .header {
    padding: 15px 20px;
  }

  .nav-items ul {
    gap: 20px;
  }

  .nav-items button {
    margin-left: 20px;
    padding: 8px 20px;
  }
}

@media (width <= 768px) {
  .header {
    flex-direction: column;
    padding: 15px;
  }

  .nav-items {
    flex-direction: column;
    width: 100%;
  }

  .nav-items ul {
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
    margin-top: 15px;
  }

  .nav-items button {
    width: 80%;
    max-width: 200px;
    margin-top: 15px;
    margin-left: 0;
  }

  .news-container {
    grid-template-columns: 1fr;
  }

  .news-card h2 {
    font-size: 1.2rem;
  }

  .news-card p {
    font-size: .85rem;
  }
}

.news-image {
  object-fit: cover;
  border-radius: 8px 8px 0 0;
  width: 100%;
  height: 250px;
}

.news-description {
  color: var(--text-muted);
  flex-grow: 1;
  margin-bottom: 15px;
  font-family: Space Mono, monospace;
  font-size: .9rem;
  line-height: 1.5;
  overflow: hidden;
}

.read-more-button {
  background-color: var(--accent-red);
  letter-spacing: .3px;
  border: none;
  border-radius: 6px;
  align-self: flex-start;
  margin-top: 15px;
  padding: 10px 18px;
  font-family: Inter, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .3s, transform .2s, box-shadow .3s;
  box-shadow: 0 3px 8px #0000004d;
  color: #fff !important;
}

.read-more-button:hover {
  background-color: #cc0811;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px #0006;
}

.news-date {
  color: var(--text-muted);
  border-top: 1px solid var(--border-dark);
  margin-top: 10px;
  padding-top: 10px;
  font-size: .8rem;
  font-style: italic;
}

body.light-mode .news-date {
  border-top-color: var(--border-light);
}
